@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
.App {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background: transparent;
  color: #212121;
  font-family: "Open Sans", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

body {
  margin: 0%;
  padding: 0%;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.joinChatContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  padding-bottom: 30px;
}

.joinChatContainer h3 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  z-index: 2;
}

form {
  margin: 10px;
}

.effect~.focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #4caf50;
  transition: 0.4s;
}

.effect:focus~.focus-border {
  width: 100%;
  transition: 0.2s;
}

.joinChatContainer input {
  width: 100%;
  border: 0;
  outline: 0;
  padding: 0.5rem 0;
  border-bottom: 2px solid #d3d3d3;
  box-shadow: none;
  font-size: 16px;
  color: #111;
  background: transparent;
}

.input-field {
  position: relative;
  width: 250px;
  height: 44px;
  line-height: 44px;
  margin: 20px;
}

.joinChatContainer button {
  width: 225px;
  height: 50px;
  margin: 7px;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  background: #4caf50;
  /* background: #4852ad; */
  color: #fff;
  cursor: pointer;
  z-index: 1;
  transition: 0.2s all ease-in-out;
}

.LeaveBut {
  width: 400px;
  height: 50px;
  margin-bottom: 7px;
  border-radius: 6px;
  background: rgb(255, 0, 0);
  font-size: 16px;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.LeaveBut:hover {
  background: rgb(207, 0, 0);
}

.joinChatContainer button:hover {
  background: #3d4694;
}

img {
  width: 12%;
  height: 23%;
  display: flex;
  z-index: 3;
}

.chat-window {
  width: 400px;
  height: 420px;
  margin: 30px;
  padding-bottom: 60px;
}

.chat-window p {
  margin: 0;
}

.chat-window .chat-header {
  height: 45px;
  border-radius: 6px;
  background: #263238;
  position: relative;
  cursor: pointer;
}

.chat-window .chat-header p {
  display: block;
  padding: 0 1em 0 2em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
}

.chat-window .chat-body {
  height: calc(450px - (45px + 70px));
  border: 1px solid #263238;
  background: #fff;
  position: relative;
}

.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}

.chat-window .chat-body .message {
  height: auto;
  padding: 10px;
  display: flex;
}

.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  min-height: 40px;
  max-width: 200px;
  background-color: #43a047;
  border-radius: 5px;
  color: white;
  display: flex;
  align-items: center;
  margin-right: 5px;
  margin-left: 5px;
  padding-right: 7px;
  padding-left: 5px;
  overflow-wrap: break-word;
  word-break: break-word;
}

#you {
  justify-content: flex-end;
}

#you .message-content {
  justify-content: flex-end;
  align-items: right;
}

#you .message-meta {
  justify-content: flex-end;
  margin-right: 7px;
}

#other {
  justify-content: flex-start;
}

#other .message-content {
  justify-content: flex-start;
  background-color: cornflowerblue;
}

#other .message-meta {
  justify-content: flex-start;
  margin-right: 7px;
}

.message-meta #author {
  margin-left: 10px;
  font-weight: bold;
}

.chat-window .chat-body .message .message-meta {
  display: flex;
  font-size: 12px;
}

.chat-window .chat-footer {
  height: 40px;
  border: 1px solid #263238;
  border-top: none;
  display: flex;
}

.chat-window .chat-footer input {
  height: 100%;
  flex: 85% 1;
  border: 0;
  padding: 0 0.7em;
  font-size: 1em;
  border-right: 1px dotted #607d8b;
  outline: none;
  font-family: "Open Sans", sans-serif;
  z-index: 2;
}

.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15% 1;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 25px;
  color: lightgray;
  z-index: 3;
}

.chat-window .chat-footer button:hover {
  color: #43a047;
}

.hide {
  opacity: 0 !important;
}
